import React, { useCallback, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Button, Radio, RadioGroup
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import Icon from '../../../components/Icon';
import style from './PullFrom.module.css';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import {
  fetchPlaidToken, linkPlaidAccount, listSelfBanks, listSelfCards, removeSelfBank
} from '../../../store/middlewares/account';
import { apiErrorHandler, showNotification } from '../../../utils/general';
import OpenPlaidLink from '../../../components/Plaid';
import { useDrawer } from '../../../hooks/drawer';

const PullFundsInfo = React.lazy(() => import('../PullFundsInfo/PullFundsInfo'));
const LinkCard = React.lazy(() => import('../LinkCard/LinkCard'));

// @TODO: Needs to create seperate page for bank and card

/**
 * Pull funds options page UI
 * @returns {React.ReactNode}
 */
const PullFrom = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open, close, data: drawerData } = useDrawer<{option: 'bank' | 'card'}>();

  const { option = 'bank' } = drawerData || {};
  const { selectedAccount: account, selfBankAccounts, linkedCards } = useAppSelector((store) => store.account, shallowEqual);

  const [plaidToken, updatePlaidToken] = useState<string | null>(null);
  const [selectedContact, selectContact] = useState('');

  const getPlaidToken = useCallback(() => {
    if (account) {
      dispatch(fetchPlaidToken(account?.id))
        .then((data) => {
          updatePlaidToken(data.linkToken);
        })
        .catch(apiErrorHandler);
    }
  }, [account]);

  const plaidOnSuccess = useCallback(
    // eslint-disable-next-line camelcase
    (public_token: string, metadata: any) => {
      updatePlaidToken(null);
      if (account?.id) {
        dispatch(
          linkPlaidAccount({
            accountId: account.id,
            plaidAccountId: metadata.accounts[0]?.id,
            // eslint-disable-next-line camelcase
            plaidToken: public_token,
          })
        ).catch(() => {
          showNotification('An error occurred.', 'Unable to verify plaid account', 'error');
        });
      }
    },
    [account]
  );

  useEffect(() => {
    if (account) {
      if (option === 'bank') {
        dispatch(listSelfBanks(account.id));
      } else {
        dispatch(listSelfCards(account.id));
      }
    }
  }, [option, account]);

  return (
    <div className="page">
      <MiniBox
        heading={t('pull_funds_from.title')}
        showBack
        footer={(
          <Button
            width="100%"
            variant="primary"
            disabled={selectedContact === ''}
            onClick={() => {
              open<{option: string, contactId: string}>(PullFundsInfo, {
                option,
                contactId: selectedContact
              });
            }}
          >
            {t('pull_funds_from.ctaText')}
          </Button>
        )}
        onBack={() => {
          close();
        }}
      >
        <Box width="100%">
          {selfBankAccounts.length !== 0 && option === 'bank' ? (
            <>
              <Text variant="large" className={style.pullFrom}>{t('pull_funds_from.pullFundsFrom')}</Text>

              <Box className={style.bankList}>
                <RadioGroup onChange={selectContact} value={selectedContact}>
                  {selfBankAccounts.map((b) => (
                    <Box key={b.id} className={style.bankInfo}>
                      <Radio value={b.id}>
                        <Text variant="regular" className="text-dark">
                          {b.ach.bankName}
                          {' '}
                          XXXXXX
                          {' '}
                          {b.ach.accountNumber.slice(b.ach.accountNumber.length - 4)}
                        </Text>
                      </Radio>
                      <Text
                        className={style.removeBank}
                        onClick={() => {
                          dispatch(removeSelfBank(b.id));
                        }}
                      >
                        {t('pull_funds_from.remove')}

                      </Text>
                    </Box>
                  ))}
                </RadioGroup>

              </Box>

            </>
          ) : null }

          {linkedCards.data.length !== 0 && option === 'card' ? (
            <>
              <Text variant="large" className={style.pullFrom}>{t('pull_funds_from.pullFundsFrom')}</Text>

              <Box className={style.bankList}>
                <RadioGroup onChange={selectContact} value={selectedContact}>
                  {linkedCards.data.map((c) => (
                    <Box key={c.id} className={style.bankInfo}>
                      <Radio value={c.id}>
                        <Text variant="regular" className="text-dark">
                          XXXX XXXX XXXX
                          {' '}
                          {c.debitCard?.last4}
                        </Text>
                      </Radio>
                      <Text
                        className={style.removeBank}
                        onClick={() => {
                          dispatch(removeSelfBank(c.id, 'card'));
                        }}
                      >
                        {t('pull_funds_from.remove')}

                      </Text>
                    </Box>
                  ))}
                </RadioGroup>

              </Box>

            </>
          ) : null }

          <Text variant="large" className={style.linkAccount}>{t(option === 'bank' ? 'pull_funds_from.linkAccount' : 'pull_funds_from.linkCard')}</Text>

          <Button
            width="100%"
            variant="unstyled"
            onClick={() => {
              if (option === 'bank') {
                getPlaidToken();
              } else {
                open(LinkCard);
              }
            }}
          >
            <HStack className={style.transferType}>
              <Box>
                <Text variant="regular">{t(option === 'bank' ? 'pull_funds_from.linkAnotherAccount' : 'pull_funds_from.linkAnotherCard')}</Text>
              </Box>
              <Icon icon="Arrow-small-right" size={8} className="svg-light-grey-1" />
            </HStack>
          </Button>

          {plaidToken ? (
            <OpenPlaidLink
              token={plaidToken}
              onSuccess={plaidOnSuccess}
              onExit={() => {
                updatePlaidToken(null);
              }}
            />
          ) : null}

        </Box>
      </MiniBox>

    </div>
  );
};

export default PullFrom;
